@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,700;1,100;1,200;1,300;1,400;1,700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}
*{
  font-family: 'Montserrat', sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.leaflet-container{
  width: 100%;
  height: 88vh;
}
.text-decoration-none{
  text-decoration: none !important;
}
.leaflet-popup-content-wrapper{
  width: auto !important;
  z-index: 99999 !important;
}
.px-20{
  padding: 0px 200px !important;
}
.input-search-navbar{
  width: 300px !important;
  border-radius: 5px !important;
}
.nav-link:visited{
  color: #3D7CBF !important;
}
.navbar-title{
  font-size: 12px !important;
}
/* bootstrap */
.text-primary{
  color: #3D7CBF !important;
}
.btn-outline-primary {
  color: #0984e3 !important;
  background-color: transparent;
  background-image: none;
  border-color: #3D7CBF !important;
}
.btn-outline-primary:hover {
  background-color: transparent;
  color: #ffffff !important;
  background-color: #0984e3 !important;
}
.bg-light{
  background-color: #ffffff !important;
}
.bg-primary{
  background-color: #5086C1 !important;
}
/* border */
.br-5{
  border-radius: 5px !important;
}
.br-10{
  border-radius: 10px !important;
}
.br-full{
  border-radius: 50% !important;
}
/* font */
.font-14{
  font-size: 14px !important;
}
.font-18{
  font-size: 18px !important;
}
.font-20{
  font-size: 20px !important;
}
.font-24{
  font-size: 24px !important;
}
.shadow{ 
box-shadow: -1px 0px 14px 4px rgba(0,0,0,0.44);
}
.overflow-hidden{
  overflow: hidden;
}
@media only screen and (max-width: 770px) {
  .nav-text-services{
    font-size: 12px !important;
  }
  .input-search-navbar{
    width: 120px !important;
    margin-right: 10px !important;
  }
}
